.App {
  text-align: center;
  margin: 0;
  padding: 0;
}

.App-logo {
  height: auto;
  pointer-events: none;
  width: 95vw;
  margin-right: 0.25em;
}

#left {
  margin-top: 1em;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
  font-family: 'Times New Roman', Times, serif;
  display: flex;
  justify-content: center;
  align-items: flex-end; 
  flex-flow: column;
}

.App-link {
  color: #61dafb;
}

#list {
  text-align: left;
}

.li-label {
  margin-right: 0.25em;
}

.bottom-arrow {
  width: 128px;
  height: auto;
}

